import React, { Component } from 'react';
import { connect } from 'react-redux'

import './Home.scss';

import background_dark from '../../assets/background_black_code_trim.mov'
import background_white from '../../assets/background_white_code.mov'

class Home extends Component {

    changeName = (event)=>this.props.changeName(event.target.value)

    toggleDarkMode = ()=>{this.props.toggleDarkMode(this.props.isDarkMode)}

    componentDidMount() {

    }

    render() {
        let name = this.props.name;
        let isDarkMode = this.props.isDarkMode;
        return (
            <div className="Home">
                <React.Fragment key={this.props.isDarkMode}>
                    <video autoPlay muted loop id="background-video"><source src={isDarkMode? background_dark: background_white} type="video/mp4"/></video>
                </React.Fragment>
                <div>
                    <h1>{name}</h1>
                    <p>This is my personal website</p>
                    <input type="text" name="newName" onChange={this.changeName}></input>
                    <label className="switch">
                        <input type="checkbox" name="isDarkMode" onChange={this.toggleDarkMode} className="checkbox"></input>
                        <span class="slider round"></span>
                    </label>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state)=>{
  return {
    isDarkMode: state.isDarkMode,
    name: state.name
  }
}

const mapDispatchToProps = (dispatch)=>{
    return {
        changeName: (name)=> {dispatch({type:'CHANGE_NAME', name:name})},
        toggleDarkMode: (isDarkMode)=>{dispatch({type:'TOGGLE_DARK_MODE', isDarkMode:isDarkMode})}
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);